<template>
    <div class="container">
        <div class="header">
            touch the art.
        </div>
        <div class="exhibits">
            <div class="page" v-for="(page, i) in layout" :key="'page' + i" :style="{transform}">
                <v-exhibit v-for="(exhibit, j) in page" :key="'Exhibit ' + j" :color="getColor(i, j)" :exhibit="exhibit"/>
            </div>
        </div>
        <div class="controls">
            <button @click="previous" :style="{visibility: page > 0 ? 'visible' : 'hidden'}" class="back">
                <span class="text">back</span>  
                <span class="arrow">←</span>
            </button>
            <button @click="next" :style="{visibility: !end ? 'visible' : 'hidden'}" class="next">
                <span class="text">next</span>
                <span class="arrow">→</span>
            </button>
        </div>
    </div>
</template>
<script>

import VExhibit from './VExhibit'
import exhibits from '../assets/exhibits'
export default {
    components: {VExhibit},
    data: () => ({
        // colors: ["3f7cac","95afba","bdc4a7","d5e1a3","e2f89c","c64191","885053","fe5f55"],
        colors: ["14342b","60935d","bab700","bbdfc5","ff579f","fb3640","247ba0","e2e2e2"],
        lastpicked: -1,
        exhibits,
        page: 0,
        width: window.innerWidth,
        scrolling: false
    }),
    mounted(){
        window.onwheel = ({deltaY}) => {
            if(this.scrolling) return
            this.scrolling = true
            if(deltaY < 0) this.previous()
            else this.next()
            setTimeout(() => {if(this.scrolling) this.scrolling = false}, 800)
        }
    },
    methods: {
        getColor(i, j){
            const index = this.layout[0].length * i + j
            return '#' + this.colors[ index % this.colors.length ]
        },
        next() {
            if(this.page < 20 - 1) this.page++;
        },
        previous() {
            if(this.page > 0) this.page -= 1;
        },
    },
    computed: {
        transform() {
            return `translateX(${this.page * -1 * this.width/2}px`
        },
        layout() {
            const {width, exhibits} = this
            const exhibitWidth = 400
            const exhibitsPerPage = Math.floor(width / exhibitWidth) || 1
            const layout = []
            for(let i = 0; i < exhibits.length; i += exhibitsPerPage){
                layout.push(exhibits.slice(i, i+exhibitsPerPage))
            }
            return layout
        },
        end() {
            return this.page == 20 - 1
        }
    }
}
</script>
<style lang="scss" scoped>
.container {
    background: #f1eeec;
    height: 100vh;
    display: flex;
    align-items: center;
}
.header {
    position: fixed;
    color: rgb(131, 131, 131);
    top: 0;
    left: 0;
    padding: 10px;
    text-align: center;
    font-size: 30px;
    width: 100%;
}
.exhibits {
    display: flex;
    overflow: hidden;
    width: 100%;
}
.page {
    flex: 0 0 100vw;
    display: flex;
    justify-content: space-evenly;
    transition: ease 0.8s all;
}
.controls {
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 70px;
    button {
        border: none;
        font-size: 50px;
        background: transparent;
        color: rgb(129, 129, 129);
        cursor: pointer;
        outline: none;
        padding: 15px;
    }
    button::last-of-type {
        flex-direction: row-reverse;
    }
    button:hover {
        color: #c75000;
    }
    button.next {
        display: flex;
        flex-direction: column;
        &:hover {
            transform: rotate(-10deg) translateY(-100%);
        }
        .text {
            display: block;
        }
        .arrow {
            // display: block;
            transform: rotate(10deg);
        }
    }
    button.back {
        display: flex;
        flex-direction: column;
        &:hover {
            transform: rotate(10deg) translateY(-100%);
        }
        .text {
            display: block;
        }
        .arrow {
            // display: block;
            transform: rotate(-10deg);
        }
    }
}
</style>
